import React, { isValidElement } from 'react';
import Types from 'prop-types';

import Alert, { AlertType, AlertSize, AlertArrowPosition } from '../alert/Alert';

import { isString, isArray } from '../common/validation/type-validators';

const MessageControl = ({ type, content }) => {
  function getMessageContent() {
    if (isString(content)) {
      if (isImagePath(content)) {
        return <img className="thumbnail m-y-2" src={`${content}`} alt="" />;
      }
      return content;
    }
    if (isArray(content) && content.length > 0) {
      return content.map((message, index) => <div key={index.toString()}>{message}</div>);
    }
    if (isValidElement(content)) {
      return content;
    }
    // eslint-disable-next-line
    console.error('Provided content is not supported', content);
    return null;
  }

  function isImagePath(string) {
    return string.match(/.jpg|.jpeg|.png/g);
  }

  const messageContent = getMessageContent();

  return messageContent ? (
    <Alert type={type} arrow={AlertArrowPosition.UpLeft} size={AlertSize.Small}>
      {messageContent}
    </Alert>
  ) : null;
};

MessageControl.propTypes = {
  type: Types.oneOf(Object.values(AlertType)),
  content: Types.oneOfType([Types.string, Types.node, Types.arrayOf(Types.string)]).isRequired,
};

MessageControl.defaultProps = {
  type: AlertType.Info,
};

export default MessageControl;



